import Vue from "vue";
import Vuex from "vuex";
import whereamiApi from "./api/whereamiApi";

Vue.use(Vuex);

const READ_WHEREAMI_SUCCESSFUL = "READ_WHEREAMI_SUCCESSFUL";

const state = {
  whereami: {}
};

const mutations = {
  [READ_WHEREAMI_SUCCESSFUL](state, data) {
    state.whereami = data;
  },
};

const actions = {
  getWhereami({ commit }) {
    whereamiApi.getWhereami().then((response) => {
      commit(READ_WHEREAMI_SUCCESSFUL, response.data);
    });
  },
};

const getters = {
  whereami() {
    return state.whereami;
  },
};

export default new Vuex.Store({
  state,
  mutations,
  actions,
  getters,
});